.hero {
  background-image: url("../fundo.c533c860.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 64px;
  padding-bottom: 100px;
  position: relative;
}

.hero:before {
  opacity: .6;
  content: " ";
  background-color: #000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.hero__text {
  color: #fff;
  text-align: center;
  margin-top: 64px;
  margin-bottom: 80px;
  font-size: 40px;
  font-weight: 800;
  line-height: 56px;
}

@media (width <= 640px) {
  .hero__text {
    margin: 24px 0;
    font-size: 22px;
    line-height: 24px;
  }
}

.hero__logo {
  height: 80px;
  margin: 0 auto;
  display: block;
}

@media (width <= 640px) {
  .hero__logo {
    height: 40px;
  }
}

.hero .container {
  position: relative;
}

@media (width <= 640px) {
  .hero .container {
    display: block;
  }

  .hero {
    padding: 40px 0;
  }
}

